nav {
position: fixed;
top: 0;
width: 100%;
height: 50px;
background: linear-gradient(45deg, rgb(0, 80, 107), lightblue);
}

.liste {
    list-style-type: none;
    background: linear-gradient(45deg, rgb(0, 80, 107), lightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}

.items {
    text-transform: uppercase;
    color: #f1f1f1;
    cursor: pointer;
    padding:1em;
    

}

.items a{
    color: #F1F1F1;

    text-decoration: none;
}



.btn{
    display: none;
    position: absolute;
    right: 10px;
    top:7px;
    padding: 5px;
    font-size: 18px;
    cursor: pointer;
    border: none;
    background: none;
    
    

}

.distransicslogo{
    cursor: pointer;
    position: fixed;
    top: 2px;
    left: 50px;

}

@media screen and (max-width :480px) {

    .distransicslogo{
        cursor: pointer;
        display: inline-block;
        left: 10px;
        top:10px;
    
    }

    .liste{
        flex-direction: column;
        height: auto;
        font-size: 20px;
    }

    .items:nth-child(1){
        
        margin-top: 5px;
    }

    

    .items{
        height: 50px;
        width: 100%;
        border-bottom: 1px solid rgba(255, 255, 255, 0.6);
        line-height: 40px;
        padding:0em;
        text-align: center;

    }

    
    .btn{
        display: block;
        height: 30px;
        
    }
    .MenuDropdown{
        color:white;
        height: 30px;
        width: 30px;
    }
}