.missions{
    
    margin: 100px auto 0;
    padding-left: 69%;
    height: auto;
    font-size: 29px;
}



  .App-logo {
    height: 40vmin;
    
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
   
    min-height: 50vh;
    padding-left: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }
  
 
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .p1missions,.p2missions,.p3missions{

    max-width: 700px;
    height: auto;
    font-size: 22px;
    margin-top: 4%;

}

.txsocial{
        
    float: left;
    width: 33.33%;
    padding: 5px;
    

}

.aws{
        
    float: left;
    width: 33.33%;
    padding: 5px;
    

}

@media (max-width:480px)  { 

  .missions{
    
    margin: 100px auto 0;
    padding-left: 35%;
    height: auto;
    font-size: 20px;

}

.App-header {
   
  min-height: 50vh;
  padding-left: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}



.p1missions,.p2missions,.p3missions{

  max-width: 700px;
  margin: 100px auto 0;
  margin-right: 15%;
  height: auto;
  margin-top: 4%;
  font-size: 15px;

}

.txsocial{
        
  width: 250px;
  height: 300px;
  padding-left: -16%;
  margin-left: -30%;
  object-fit:cover;
  object-position:20% 20%;
  height: auto;
  object-fit: contain;
  object-position:50% 50%;
}

.aws{
        
  width: 250px;
        height: 300px;
        padding-left: -16%;
        margin-left: -30%;
        object-fit:cover;
        object-position:20% 20%;
        height: auto;
        object-fit: contain;
        object-position:50% 50%;

}

}