.introduction{
    
    margin: 100px auto 0;
    padding-left: 67%;
    height: auto;
    font-size: 29px;

}

.p1intro,.p2intro,.p3intro{

        max-width: auto;
        height: auto;
        padding-left: 42%;
        font-size: 22px;
        margin-top: 3%;
        text-align: center;
    
}


.p4intro{

        max-width: 700px;
        height: auto;
        padding-left: 42%;
        font-size: 22px;
        margin-top: 4%;
    
    }
    
    .planningtext{
    
        max-width: 700px;
        height: auto;
        padding-left: 55%;
        margin-top: 4%;
        font-size: 15px;
    
    }
    
    
    .Planning{
        
        right:10em;
        padding-left: 16%;
        margin-left: 100px;
        margin-right: auto;
    }

    .Competences{
        
        right:10em;
        margin-left: 100px;
        padding-left: 31%;
        margin-right: auto;
        
    
    }

    .competencestext{
    
        max-width: 700px;
        height: auto;
        padding-left: 65%;
        margin-top: 4%;
        font-size: 15px;
    
    }


    @media (max-width:480px)  { 


        .introduction{
    
            margin: 100px auto 0;
            padding-left: 38%;
            height: auto;
            font-size: 20px;
        
        }
        
        .p1intro,.p2intro,.p3intro{
        max-width: 400px;
        height: auto;
        padding-left: 10.2%;
        text-align: justify;
        font-size: 15px;
        margin-top:2%;
        position:relative;

        }

        .Planning{
        
        margin-left: -10%;
        width: 450px;
        height: auto;
        
        object-fit: contain;
        object-position:50% 50%;
        }

        .planningtext{
    
            max-width: 700px;
            height: auto;
            padding-left: 15%;
            margin-top: 4%;
            font-size: 15px;
        
        }

        .p4intro{

            max-width: 450px;
            height: auto;
            padding-left: 10.2%;
            text-align: justify;
            font-size: 15px;
            margin-top:2%;
            position:relative;
        
        }

        .Competences{
        
            margin-left: -27%;
            width: 450px;
            height: auto;
            object-fit: contain;
            object-position:50% 50%;
            }

            .competencestext{
    
                max-width: 700px;
                height: auto;
                padding-left: 45%;
                margin-top: 4%;
                font-size: 15px;
            
            }


    }