.notfound{
   
    
    padding: 70px;
    font-size: 40px;
    display: flex;
    
    justify-content: center;
    text-align: center;
    
    
   
}

.button-notfound{
    position: absolute;
    margin: 75px;
    margin: 0;
    left: 43%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    justify-content: center;
    padding: auto;
    width: 300px;
    height: 49px;
    text-decoration: none;
    display: inline-block;
    border: none;
    color: red;
    text-align: center;
    border: 2px solid red;
    transition-duration: 0.4s;
    font-weight: bold;
    border-radius: 12px;
    cursor: pointer;
    
}

.alertnotfound{
    color:red;
   margin:10px;
  
    
}

.button-notfound:hover {
    background-color: red;
    color: white;

  }
