.presentation{
    
    margin: 100px auto 0;
    padding-left: 40%;
    height: auto;
    font-size: 29px;

}


.imagesDIS{
        
    
    padding-left: 4%;
    margin-left: 100px;
    margin-right: auto;
    

}

.p1presentation,.p2presentation,.p3presentation,.p4presentation,.p5presentation,.p6presentation,.p7presentation{

    max-width: 700px;
    height: auto;
    padding-left: 33%;
    text-align: justify;
    font-size: 22px;
    margin-top:2%;
    position:relative;
    color: rgb(61, 61, 61);
   
}

.p8presentation{

    max-width: 950px;
    height: auto;
    padding-left: 27%;
    text-align: justify;
    font-size: 22px;
    margin-top:2%;
    position:relative;
    color: rgb(61, 61, 61);
}

.ppresentation{
    max-width: 900px;
    height: auto;
    padding-left: 29%;
    text-align: justify;
    font-size: 22px;
    margin-top:2%;
    position:relative;

}


.clients,.editlogiciel,.goorganization,.mdhome{
    color:#F18500;
    margin-right: 10px;
    width: 30px;
    height: 30px;
    
}

.gestion{
    
    margin: 100px auto 0;
    padding-left: 37%;
    height: auto;
    font-size: 29px;
    font-weight: normal;

}


.txvisio,.txvisio2,.txvisio3{
        
    right:10em;
    margin-left: 100px;
    margin-right: auto;
    

}

.txvisiotext{
    
    max-width: 700px;
    height: auto;
    padding-left: 49%;
    margin-top: 4%;
    text-align: justify;
    font-size: 15px;

}

.outilsdonnees{

    margin: 100px auto 0;
    padding-left: 27%;
    height: auto;
    font-size: 29px;
    font-weight: normal;
    
}

@media (max-width:480px)  { 




    .presentation{
    
        margin: 100px auto 0;
        padding-left: 25%;
        height: auto;
        font-size: 20px;
    
    }
    
    
    .imagesDIS{
            
        
        
        
       
        width: 410px;
        height: 300px;
        padding-left: -16%;
        margin-left: -10px;
        object-fit:cover;
        object-position:20% 20%;
        
    
    }
    
    .p1presentation,.p2presentation,.p3presentation,.p4presentation,.p5presentation,.p6presentation,.p7presentation,.p8presentation{
    
        max-width: 400px;
        height: auto;
        padding-left: 7.2%;
        text-align: justify;
        font-size: 15px;
        margin-top:2%;
        position:relative;
        color: rgb(61, 61, 61);
       
    }
    
   
    
    .ppresentation{
        max-width: 420px;
        height: auto;
        padding-left: 3.5%;
        text-align: justify;
        font-size: 15px;
        margin-top:2%;
        position:relative;
    
    }
    
    
    .clients,.editlogiciel,.goorganization,.mdhome{
        color:#F18500;
        margin-right: 10px;
        width: 30px;
        height: 30px;
        
    }
    
    .gestion{
        
        max-width: 400px;
        margin: 100px auto 0;
        padding-left: 17%;
        height: auto;
        font-size: 20px;
        font-weight: normal;
    
    }
    
    
    .txvisio,.txvisio2,.txvisio3{
            
       
   
        width: 450px;
        height: 300px;
        padding-left: -16%;
        margin-left: -2px;
        object-fit:cover;
        object-position:20% 20%;
        height: auto;
        object-fit: contain;
        object-position:50% 50%;
       

        
    
    }
    
    .txvisiotext{
        
        max-width: 400px;
        height: auto;
        padding-left: 22%;
        margin-top: 4%;
        text-align: justify;
        font-size: 15px;
    
    }
    
    .outilsdonnees{
    
         
        max-width: 400px;
        margin: 100px auto 0;
        padding-left: 17%;
        height: auto;
        font-size: 20px;
        font-weight: normal;
        
    }
    



}


